body {
  margin: 0;
  font-family: Habibi;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
p {
  color: blue;
  font-size: 20px;
}
.container {
  margin: 5%;
}
.navBar {
  background-color: #f7f7f7;
}
.navBar > ul {
  list-style: none;
  display: flex;
}
.navBar > ul > li {
  margin: 5px;
  padding: 4px;
  cursor: pointer;
}
.navBar > ul > li:hover {
  /* background-color:crimson; */
  color: crimson;
}
.title {
  padding: 4px;
  font-size: 18px;
}
.description {
  padding: 4px;
}
.footer {
  background-color: #f7f7f7;
}
.footer > div > ul {
  list-style: none;
  display: flex;
}
.footer > div > ul > li {
  margin: 2px;
  padding: 2px;
  cursor: pointer;
}
.show {
  height: 60vh;
}
.row {
  display: flex;
}
.col-md-2 {
  width: 20%;
}
.col-md-3 {
  width: 30%;
}
.col-md-9 {
  width: 70%;
  padding: 10px;
}
.sidebar {
  background-color: #f7f7f7;
}
.sidebar > ul {
  list-style: none;
}
.sidebar > ul > li {
  padding: 5px 5px 5px 5px;
  width: fit-content;
}
.sidebar > ul > li:hover {
  background-color: #ddd;
  color: #fff;
  cursor: pointer;
}
.form-group {
  margin-bottom: 5px;
}
.form-control {
  padding: 4px;
}
.btn {
  padding: 5px 10px 5px 10px;
  cursor: pointer;
}
.btn-primary {
  background-color: #f7f7f7;
  border-color: #f7f7f7;
}
.btn:hover {
  background-color: blue;
  color: #fff;
}
.card {
  padding: 1px 1px 10px 10px;
  margin: 0px 5px 0px 5px;
  background-color: #f7f7f7;
  border-radius: 4px;
}
.social-links {
  cursor: pointer;
}
